.shadowBox{
    box-shadow: inset 0 0 0 1px #16073c, 0px 32px 43px rgb(0 0 0 / 60%);
    border-radius: 10px;
    background: #1b043b;
    color: #a4a5b8!important;
    transition: transform 0.5s ease;
}
.shadowBox:hover {
    transform: scale(1.1);
  }
